import axios from 'axios';
import { FETCH_VIDEOGAMES_REQUEST, FETCH_VIDEOGAMES_SUCCESS, FETCH_VIDEOGAMES_FAILURE } from './types';

export function fetchVideoGamesRequest() {
  return {
    type: FETCH_VIDEOGAMES_REQUEST
  };
}

export function fetchVideoGamesSuccess(videoGames) {
  return {
    type: FETCH_VIDEOGAMES_SUCCESS,
    payload: videoGames
  };
}

export function fetchVideoGamesFailure(error) {
  return {
    type: FETCH_VIDEOGAMES_FAILURE,
    payload: error
  };
}

export function fetchVideoGames() {
  return async function (dispatch) {
    dispatch(fetchVideoGamesRequest());
    try {
      const response = await axios.get('/videogames');
      dispatch(fetchVideoGamesSuccess(response.data));
    } catch (error) {
      dispatch(fetchVideoGamesFailure(error));
    }
  };
}
