import React from 'react';
import styles from './VideoGameCard.module.css';

const VideoGameCard = ({ videoGame }) => {
  return (
    <div className={styles.card}>
      <img src={videoGame.image} alt={videoGame.name} className={styles.image} />
      <div className={styles.content}>
        <h3 className={styles.title}>{videoGame.name}</h3>
        <p className={styles.genre}>Genres: {videoGame.genres.join(', ')}</p>
      </div>
    </div>
  );
};

export default VideoGameCard;